
.report h1{
	padding: 20px;
	font-size: 24px;
	background: rgb(51,136,203);
	color: #fff;
	font-weight: normal;
}
.report .title{
	margin: 25px 0 20px 30px;
	font-size: 20px;
}
.report .base p{
	padding: 4px 0;
}
.report .main{
	margin: 15px;
	padding: 30px 15px 30px;
	border-color: #eee;
	border-radius: 1px;
	box-shadow: 0px 0px 10px 3px rgba(0,0,0,.1);
	margin-bottom: 22px;
	background-color: #fff;
	border: 1px solid transparent;
}
.report .main h2{
	text-align: center;
	font-size: 20px;
	padding-bottom: 5px;
	margin-bottom: 5px;
	border-bottom: 1px solid rgb(51,136,203);
	color: rgb(51,136,203);
	font-weight: normal;
}
.report .main h3{
	font-size: 16px;
	font-weight: normal;
	padding: 10px 0;
}
.report .main p{
	text-align: left;
	font-size: 16px;
	line-height: 2;
}



