
.report .title{
	margin: 25px 0 20px 30px;
	font-size: 20px;
}
.report .base p{
	padding: 4px 0;
}
.report .main{
	margin: 15px;
	padding: 30px 15px 30px;
	border-color: #eee;
	border-radius: 1px;
	box-shadow: 0px 0px 10px 3px rgba(0,0,0,.1);
	margin-bottom: 22px;
	background-color: #fff;
	border: 1px solid transparent;
}
.report .main h2{
	text-align: center;
	font-size: 20px;
	padding-bottom: 5px;
	margin-bottom: 5px;
	border-bottom: 1px solid rgb(51,136,203);
	color: rgb(51,136,203);
	font-weight: normal;
}
.report .main h3{
	font-size: 16px;
	font-weight: normal;
	padding: 10px 0;
}
.p1{
	font-size: 16px;
	line-height: 2;
	text-align: center;
}
.back{
	display: -webkit-box;
	display: -webkit-flex;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: start;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-box-align: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 80vh;
	margin-top: 3vw;
}
.er2{
	width: 80vw;
}



